import React from "react";
import Container from "react-bootstrap/Container";
import Jumbotron from "react-bootstrap/Jumbotron";

const Footer = (props) => {
  const gradient = "#4484ce, #1ad7c0, #ff9b11, #9b59b6, #ff7f7f, #ecf0f1"
  const bgStyle = { background: `linear-gradient(136deg,${gradient})`, 
  backgroundSize: "1200% 1200%", color:"white" }; //#f5f5f5 original background color

  return (
    
    <footer fluid style={bgStyle} className="mt-auto py-5 text-center ">
      
      <Container>
        {props.children}
        <i className="fas fa-code" /> with <i className="fas fa-heart" /> by{" "}
        <a
          className="badge badge-dark"
          rel="noopener"
          href="https://github.com/lpdabest1"
          aria-label="My GitHub"
        >
          Lorenzo Pesina
        </a>{" "}
        using <i className="fab fa-react" />
        <p>
          <small className="text-muted">
            {/*Project code is open source from hashirshoaeb. Feel free to fork and make your own
            version.*/}
          </small>
        </p>
      </Container>
    </footer>
  );
};

export default Footer;
